<template>
    <div class="stuworkdetail">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 课程设计
                </el-breadcrumb-item>
                <el-breadcrumb-item v-if="items">{{items.teaching.name}}</el-breadcrumb-item>
            </el-breadcrumb> 
        </div>
        <div class="container" v-loading="loading">
            <div class="modify-button" v-if="status==1">
              <el-button type="danger" size="small" @click="handleDelete">退回</el-button>
              <el-button type="primary" @click="editVisible = true" size="small">评分</el-button>
            </div>
            <div class="suc" v-if="status==3">
              <el-alert
                  title="作业已完成"
                  :closable="false"
                  effect="dark"
                  type="success">
                </el-alert>
            </div>
            <div class="suc" v-if="status==2">
              <el-alert
                  title="作业已退回"
                  :closable="false"
                  effect="dark"
                  type="error">
                </el-alert>
            </div>
            <div class="form"  v-if="items">
                <h3>{{items.teaching.name}}</h3>
                <el-form label-width="90px">
                    <el-form-item class="work-date" size="small" label="指导老师：">
                        {{items.teaching.teacherName}}
                    </el-form-item>
                    <el-form-item class="work-date" size="small" label="学生：">
                        {{items.studentName}}
                    </el-form-item>
                    <el-form-item class="work-date" size="small" label="作业项目：">
                        {{items.workProjectInfoDTO.name}}
                    </el-form-item>
                    <el-form-item label="项目简介：">
                        <div class="teacherishi">{{items.workProjectInfoDTO.desc}}</div>
                    </el-form-item>
                    <el-form-item label="相关资料：">
                        <template v-for="item in items.workProjectInfoDTO.filelist">
                        <a target="_blank" :href="downurls+item.url" class="download-bg"  >
                          <el-button class="download" type="text">{{item.name}}</el-button>
                          <i class="el-icon-download"></i>
                        </a><br />
                        </template>
                    </el-form-item>
                    <el-form-item label="参考答案：">
                        <template v-for="item in items.workProjectInfoDTO.referenceSolutionList">
                        <a target="_blank" :href="downurls+item.url" class="download-bg"  >
                          <el-button class="download" type="text">{{item.name}}</el-button>
                          <i class="el-icon-download"></i>
                        </a><br />
                        </template>
                    </el-form-item>
                    <el-form-item label="工艺卡片：">
                        <!-- <el-form v-for="(item,index) in craftCardInfoDTOList" :key="index" ref="form" label-width="85px" class="form-in">
                          <h4>工序{{item.sort+1}}</h4>
                          <el-form-item class="work-date" size="small" label="加工表面：">
                              {{item.surfaceName}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="加工形式：">
                              {{item.processingMethodName}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="工序内容：">
                              <div class="teacherishi">{{item.processContent}}</div>
                          </el-form-item>
                        </el-form> -->

                        <div class="demo-input-size" style="margin-top:10px;overflow:hidden;">
                        <el-input size="small" class="fl" style="width:45%;margin-right:9%;" placeholder="请输入内容" disabled v-model="results.partNumber">
                          <template slot="prepend">零件号</template>
                        </el-input>
                        <el-input size="small" disabled class="fl" style="width:45%;" placeholder="请输入内容" v-model="results.materials">
                          <template slot="prepend">材料</template>
                        </el-input>
                      </div>
                      <div class="demo-input-size" style="margin-top:10px;overflow:hidden;">
                        <el-input size="small" disabled class="fl" style="width:45%;margin-right:9%;" placeholder="请输入内容" v-model="results.partName">
                          <template slot="prepend">零件名称</template>
                        </el-input>
                        <el-input size="small" disabled class="fl" style="width:45%;" placeholder="请输入内容" v-model="results.workblankWeight">
                          <template slot="prepend">毛坯重量</template>
                        </el-input>
                      </div>
                      <div class="demo-input-size" style="margin-top:10px;overflow:hidden;">
                        <el-input size="small" class="fl" style="width:45%;margin-right:9%;" placeholder="请输入内容" disabled v-model="results.productType">
                          <template slot="prepend">生产类型</template>
                        </el-input>
                        <el-input size="small" disabled class="fl" style="width:45%;" placeholder="请输入内容" v-model="results.workblankKind">
                          <template slot="prepend">毛坯种类</template>
                        </el-input>
                      </div>

                        <el-form  ref="form" v-for="(item, index) in results.processList" :key="index" label-width="85px" :class="{'form-in':true,'overflow':item.close}">
                          <h4><i class="iconstyle el-icon-plus" v-if="item.close" @click="closetrue(index)"></i><i class="iconstyle el-icon-minus" v-if="!item.close" @click="closefalse(index)"></i><span style="color:#000;font-size:16px;">工序 {{index+1}} </span><template  v-if="item.errorMsg"><span style="color:red;font-size:12px;font-weight:500;" v-for="(it,index6) in item.errorMsg"> ({{index6+1}})不符合{{it}}</span></template>
                          </h4>
                          <el-form-item class="work-date" size="small" label="工序名称：">
                            {{item.name}}
                          </el-form-item>
                          <el-form-item class="work-date" size="small" label="加工表面：">
                              <el-select class="margin10" disabled v-model="item.surfaceId" size="mini" placeholder="请选择表面类型">
                                <el-option
                                  v-for="item in biaolist"
                                  :key="item.surfaceId"
                                  :label="item.name"
                                  :value="item.surfaceId">
                                </el-option>
                              </el-select>
                          </el-form-item>
                          <el-form-item class="work-date work-date-cont" size="small" label="工序简图：">
                              <div class="picload-bg">
                                <a target="_blank" :href="downurls+item.processFile.path" class="download-bg"  ><img :src="downurls+item.processFile.path" class="fl imgshow" alt="" /></a>
                              </div>
                          </el-form-item>

                          <!-- 工位-start -->
                          <el-form  label-width="85px" style="margin-bottom:10px;background:#fff;" class="form-in" v-for="(item1, index1) in item.workingPositionList" :key="index1">
                            <h4><span style="color:#333;font-size:15px;">工位 {{index1+1}} </span>
                            </h4>

                            <!-- 工步-start -->
                            <el-form  label-width="85px" style="background:#fcfcfc;margin-bottom:10px;border:1px dashed #ccc;" class="form-in" v-for="(item2, index2) in item1.workingStepList" :key="index2">
                              <h4 style="border-bottom:1px dashed #ccc;"><span style="color:#666;font-size:14px;">工步 {{index2+1}}</span> 
                              <template  v-if="item2.errorMsg"><span style="color:red;font-size:12px;font-weight:500;" v-for="(dat,index5) in item2.errorMsg"> ({{index5+1}})不符合{{dat}}</span></template>
                              </h4>
                              <div class="demo-input-size" style="margin-top:10px;overflow:hidden;">
                                <div class="inp-bg" style="margin-right:4%">
                                  <span class="span">机床：</span>
                                  <el-select class="margin10" disabled v-model="item2.tool" size="mini" placeholder="请选择机床">
                                    <el-option
                                      v-for="item in jichuanglist"
                                      :key="item.id"
                                      :label="item.name"
                                      :value="item.id">
                                    </el-option>
                                  </el-select>
                                </div>
                                <div class="inp-bg">
                                  <span class="span">夹具或辅助工具：</span>
                                  <el-input size="mini" disabled class="inp-cont"  placeholder="请输入内容" v-model="item2.fixture"></el-input>
                                </div>
                              </div>
                              <div class="demo-input-size" style="margin-top:10px;overflow:hidden;">
                                <div class="inp-bg" style="margin-right:4%">
                                  <span class="span">刀具：</span>
                                  <el-input size="mini" disabled class="inp-cont" placeholder="请输入内容" v-model="item2.cutter"></el-input>
                                </div>
                                <div class="inp-bg">
                                  <span class="span">走刀深度（mm）：</span>
                                  <el-input size="mini" disabled class="inp-cont"  placeholder="请输入内容" v-model="item2.feedingDepth"></el-input>
                                </div>
                              </div>
                              <div class="demo-input-size" style="margin-top:10px;overflow:hidden;">
                                <div class="inp-bg" style="margin-right:4%">
                                  <span class="span">切削深度（mm）：</span>
                                  <el-input size="mini" disabled class="inp-cont" placeholder="请输入内容" v-model="item2.cutDepth"></el-input>
                                </div>
                                <div class="inp-bg">
                                  <span class="span">进给量（mm/r)：</span>
                                  <el-input size="mini" disabled class="inp-cont"  placeholder="请输入内容" v-model="item2.load"></el-input>
                                </div>
                              </div>
                              <div class="demo-input-size" style="margin-top:10px;overflow:hidden;">
                                <div class="inp-bg" style="margin-right:4%">
                                  <span class="span">主轴转速（r/min)：</span>
                                  <el-input size="mini" disabled class="inp-cont" placeholder="请输入内容" v-model="item2.mainshaftSpeed"></el-input>
                                </div>
                                <div class="inp-bg">
                                  <span class="span">切削速度（m/min)：</span>
                                  <el-input size="mini" disabled class="inp-cont"  placeholder="请输入内容" v-model="item2.cutSpeed"></el-input>
                                </div>
                              </div>
                              <div class="demo-input-size" style="margin-top:10px;overflow:hidden;position:relative;padding-left:120px;box-sizing:border-box;">
                                <span style="position:absolute;top:0;left:0;">工时定额（min）：</span>
                                <div class="inp-bg" style="width:33%;margin-right:2%;padding-left:80px;">
                                  <span class="span">基本时间：</span>
                                  <el-input size="mini" disabled class="inp-cont" placeholder="请输入内容" v-model="item2.basicTime"></el-input>
                                </div>
                                <div class="inp-bg" style="width:33%;margin-right:2%;padding-left:80px;">
                                  <span class="span">辅助时间：</span>
                                  <el-input size="mini" disabled class="inp-cont"  placeholder="请输入内容" v-model="item2.assistTime"></el-input>
                                </div>
                                <div class="inp-bg" style="width:30%;padding-left:120px;">
                                  <span class="span">工作地服务时间：</span>
                                  <el-input size="mini" disabled class="inp-cont"  placeholder="请输入内容" v-model="item2.workspaceServiceTime"></el-input>
                                </div>
                              </div>
                            </el-form>
                            <!-- 工步-end -->


                          </el-form>
                          <!-- 工位-end -->

                        </el-form>


                    </el-form-item>
                    <el-form-item label="作业附件：">
                        <template v-for="item in items.workFileDTOList">
                        <a target="_blank" :href="downurls+item.url" class="download-bg"  >
                          <el-button class="download" type="text">{{item.name}}</el-button>
                          <i class="el-icon-download"></i>
                        </a><br />
                        </template>
                    </el-form-item>
                    <el-form-item label="作业讨论：">
                        <el-tabs type="border-card">
                          <el-tab-pane label="私信老师">
                            <talkCmp @getmore="getmore" @pus="Addmessagebyteacher" @dele="deletemessage" :workMessageInfoDTOList="workMessageInfoDTOList" :total="totalsi"/>
                          </el-tab-pane>
                        </el-tabs>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <!-- 评分弹出框 -->
        <el-dialog title="评分" :visible.sync="editVisible" width="30%">
            <el-form ref="form" label-width="70px">
                <el-form-item label="评分：">
                    <el-input size="small" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" v-model="score" maxlength="3"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="editVisible = false">取 消</el-button>
                <el-button type="primary" size="small" @click="Score">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {fetchGetworkinfobyid, fetchAddmessagebyteacher, fetchReturn, fetchScore, fetchDeleteteahcermessage,fetchTeacherlistmessage, fetchGetteachercardbyworkid, fetchFinishedsurface} from '../../axios/requests'
import {downurl} from '../../axios/index'
import talkCmp from '../common/talkCmp.vue'
export default {
    name: 'course',
    components:{talkCmp},
    data() {
        return {
          loading:false,
          status:0,
          id:'',
          items:'',
          craftCardInfoDTOList:[],
          workMessageInfoDTOList:[],

          editVisible:false,
          talktext:'',
          score:'',
          downurls:'',
          totalsi:0,
          startId:0,

          results:'',
          jichuanglist:[{
            id:1,
            name:'车床'
          },{
            id:2,
            name:'铣床'
          },{
            id:3,
            name:'刨床'
          },{
            id:4,
            name:'磨床'
          }],
          biaolist:''
        };
    },
    mounted(){
      this.downurls = downurl;
      let {id} = this.$route.params;
      this.id = id;
      this.Finishedsurface()
      this.Getteachercardbyworkid();
      this.Getworkinfobyid();
      this.Teacherlistmessage(1)
    },
    methods: {
      closetrue(index){
        this.results.processList[index].close=false;
      },
      closefalse(index){
        this.results.processList[index].close=true;
      },
      async Finishedsurface() {//得到加工表面
        try {
          let response = await fetchFinishedsurface(this.id);
          if (response.status.code ==0) {
            this.biaolist = response.result.list;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
      async Getteachercardbyworkid(){
        try {
          let response = await fetchGetteachercardbyworkid(this.id);
          if (response.status.code ==0) {
            var data = response.result;
            data.processList.forEach(item=>{
              item.close=false
            })
            console.log(data)
            this.results = data;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      // 退回操作
      handleDelete() {
          // 二次确认退回
          this.$confirm('确定要退回吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Return()
          })
          .catch(() => {});
      },
      deletemessage(e){
        this.$confirm('确定要删除吗？', '提示', {
              type: 'warning'
          })
          .then(() => {
              this.Deleteteahcermessage(e.id, e.index);
          })
          .catch(() => {});
      },
      async Deleteteahcermessage(id, index) {//删除消息
        this.loading = true;
        try {
          let response = await fetchDeleteteahcermessage(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '删除成功',
              type: 'success'
            });
            this.workMessageInfoDTOList.splice(index, 1);
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Score() {//作业评分
        let {id, score} = this;
        if (!score) {
          this.$message({
            showClose: true,
            message: '请输入评分',
            type: 'warning'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchScore(id, score);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '评分成功',
              type: 'success'
            });
            this.editVisible=false;
            this.Getworkinfobyid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Return() {//退回作业
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchReturn(id);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '退回成功',
              type: 'success'
            });
            this.Getworkinfobyid()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Addmessagebyteacher(talktext) {//添加作业消息
        let {id} = this;
        if (!talktext) {
          this.$message({
            showClose: true,
            message: '讨论内容不能为空',
            type: 'warning'
          });
          return;
        };
        this.loading = true;
        try {
          let response = await fetchAddmessagebyteacher(id, talktext);
          if (response.status.code ==0) {
            this.$message({
              showClose: true,
              message: '发表成功',
              type: 'success'
            });
            this.talktext = '';
            this.Teacherlistmessage(1)
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      async Getworkinfobyid() {//得到作业详情
        this.loading = true;
        let {id} = this;
        try {
          let response = await fetchGetworkinfobyid(id);
          if (response.status.code ==0) {
            this.items = response.result;
            this.status = response.result.status;
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          this.loading = false;
        }
      },
      getmore(pagenum){
        this.Teacherlistmessage(pagenum)
      },
      async Teacherlistmessage(pageNum) {//得到私信消息
        var size=10;
        if (pageNum==1) {
          this.workMessageInfoDTOList=[];
          this.startId=0;
          var size=5;
        };
        try {
          let response = await fetchTeacherlistmessage(this.id, pageNum, 5, this.startId);
          if (response.status.code ==0) {
            if (pageNum==1) {
              this.startId = response.result.list[0].id;
             };
            this.totalsi = response.result.total;
             this.workMessageInfoDTOList=response.result.list.reverse().concat(this.workMessageInfoDTOList)
             
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
        }
      },
    }
};
</script>

<style lang='scss'>
  .form-in{
    line-height:28px;
    h4{
      padding-left:4px;
    }
    .el-form-item__label,.el-form-item__content{
      line-height:22px;
    }
  }
  .stuworkdetail .el-input.is-disabled .el-input__inner{
    color:#666;
  }
</style>

<style lang='scss' scoped>
    .stuworkdetail{
      .crumbs{
          padding-left:20px;
        }
        .overflow{
          height:28px;
          overflow:hidden;
        }
        .iconstyle{
          color:#666;
          font-size:16px;
          padding:5px;
          cursor:pointer;
          font-weight:bold;
        }
        .inp-bg{
          width:48%;
          float:left;
          overflow:hidden;
          padding-left:120px;
          box-sizing:border-box;
          position:relative;
          .span{
            position:absolute;
            left:0;
            top:0;
          }
          .inp-cont{
            float:left;
          }
        }
        .form{
          color:#444;
          .work-date{
            margin-bottom:0;
          }
          h3{
            font-size:16px;
