            line-height:22px;
            padding-top:9px;
          }
          .download-bg{
            color:#409EFF;
            .download{
              padding:0px;
            }
          }
          .form-in{
            border:1px solid #ccc;
            padding:5px 15px 10px;
            background:#f9f9f9;
            margin-top:10px;
            .teacherishi{
              padding-top:5px;
            }
            .file-bg{
              height:30px;
              overflow:hidden;
            }
            .imgshow{
              margin-top:5px;
              width:193px;
              height:115px;
              background:#ccc;
            }
            .imgshowtishi{
              color:#999;
              font-size:12px;
              span{
                color:red;
              }
            }
            .picload{
              padding-top:55px;
              margin-left:20px;
            }
          }
          h4{
          border-bottom:1px solid #ccc;
          padding-bottom:10px;
        }
        h4 .del-button{
          float:right;
          margin-right:20px;
          margin-top:10px;
        }
        h4 .caobutton{
          float:right;
          margin-right:10px;
          margin-top:3px;
        }
          .talk-bg{
            margin-top:15px;
            
            .talk{
              overflow:hidden;
              line-height:24px;
            }
            div.pic{
              width:24px;
              height:24px;
              background:#ccc;
              border-radius:24px;
              margin-right:6px;
            }
            div.pic img{
              display:block;
              width:24px;
              height:24px;
              border-radius:24px;
            }
            p.fl{
              color:#666;
              font-size:12px;
            }
            .text{
              background:#ffeeee;
              border:1px solid #ffb8b8;
              border-radius:5px;
              width:60%;
              padding:10px 15px;
              margin-left:30px;
              line-height:22px;
              box-sizing:border-box;
            }
            .green{
              background:#f2ffeb;
              border:1px solid #a3e881;
            }
            .self{
              padding-right:30px;
              position: relative;
            }
            .deldiscuss{
              position:absolute;
              right:4px;
              top:4px;
              font-size:16px;
              cursor:pointer;
            }
          }
          .push-talk{
            width:60%;
            margin-left:30px;
            margin-top:30px;
            border:1px solid #ccc;
            border-radius:6px;
          }
          .push-button{
            padding-left:30px;
            margin-top:10px;
          }
        }
        .stu-list{
            border:1px solid #ccc;
        }
        .modify-button{
          position:fixed;
          top:0;
          right:20px;
          z-index:999;
        }
        .suc{
          position:fixed;
          top:0;
          right:0;
          width:200px;
          z-index:999;
        }
    }
</style>


